*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.maindiv{
	border: 2px solid #333;
	width: 50%;
	margin-left: 25%;
	margin-top: 50px;

}
.maindiv #mainhead
{
	margin-top: 30px;
	margin-bottom: 20px;
}

.maindiv .firstchild
{
	display: grid;
	grid-template-columns: 33% 33% 33%;
	border: 2px solid red;
	width: 60%;
	padding-top: 5px;
	margin-bottom: 15px;
}

.maindiv .firstchild button
{
	border:none;
	background: white;
	font-size: 20px;
}